<!-- script -->

<script>
import Settings from './Setting.vue';
export default {
    name: "Main",
    components:{
        Settings,
    },
    data(){
       return{
            openSetting:false,
            settingOptions:[
               {
                   title:"Edit profile",
                   click:1,
                    link:"/"
               },
               {
                   title:"profile",
                   click:2,
                    link:"/"
               },
               {
                   title:"Settings",
                   click:3,
                    link:"/"
               },
               {
                   title:"Log out",
                   click:4,
                   link:"/login"
               },
           ],
           showSetting:false,
       }
   },
   methods: {
		toggleSetting() {
			this.showSetting = !this.showSetting
		},
        openSettingsOverlay(no){
            if(no == 3){
                this.openSetting = true;
                this.showSetting = false;
            }
        }
   }
}

</script>


